'use client'
import { useEffect, useState } from 'react';
import 'flatpickr/dist/flatpickr.css';
import Label from './Label';
import { CalenderIcon } from '../../icons';
import { on } from 'events';

// Importações dinâmicas para evitar SSR
let flatpickr: any;
let Portuguese: any;
let ShortcutButtonsPlugin: any;

type PropsType = {
  id: string;
  mode?: "single" | "multiple" | "range" | "time";
  onChange?: ((selectedDates: Date[], dateStr: string, instance: any) => void);
  defaultDate?: Date | string | Date[];
  dateFormat?: string;
  label?: string;
  placeholder?: string;
  onClickHj?: ((fp: any) => void);
  onClickMes?: ((fp: any) => void);
  onClickAno?: ((fp: any) => void);
  onClickClean?: ((fp: any) => void);
};

export default function DatePicker({
  id,
  mode,
  onChange,
  label,
  defaultDate,
  dateFormat = 'd/m/Y',
  placeholder,
  onClickHj,
  onClickMes,
  onClickAno,
  onClickClean,
}: PropsType) {
  const [mounted, setMounted] = useState(false);

  if (!onClickHj) {
    onClickHj = (fp: any) => { if (fp) fp.setDate(new Date()); fp.close(); }
  }
  if (!onClickClean) {
    onClickClean = (fp: any) => { if (fp) fp.clear(); fp.close(); }
  }

  useEffect(() => {
    setMounted(true);
  }, []);

  useEffect(() => {
    if (!mounted || typeof window === 'undefined') return;

    const initFlatpickr = async () => {
      try {
        // Importações dinâmicas
        const flatpickrModule = await import('flatpickr');
        flatpickr = flatpickrModule.default;
        
        const portugueseModule = await import('flatpickr/dist/l10n/pt.js');
        Portuguese = portugueseModule.Portuguese;
        
        // Importa o plugin somente no client para evitar ReferenceError no SSR
        try {
          const shortcutModule = await import('shortcut-buttons-flatpickr');
          ShortcutButtonsPlugin = shortcutModule.default;
        } catch (e) {
          // Se falhar no SSR ou ausência do módulo, segue sem o plugin
          ShortcutButtonsPlugin = undefined;
        }

        const flatPickr = flatpickr(`#${id}`, {
          mode: mode || "single",
          static: false, // Define como false para permitir posicionamento dinâmico
          // showMonths: mode === 'range' ? 2 : 1,
          // monthSelectorType: "static", // opções "dropdown" ou "static"
          dateFormat,
          defaultDate,
          onChange,
          locale: Portuguese,
          plugins: ShortcutButtonsPlugin ? [
            ShortcutButtonsPlugin({
              button: [
                { label: "Hoje" },
                ...(onClickMes ? [{ label: "Este Mês" }] : []),
                ...(onClickAno ? [{ label: "Este Ano" }] : []),
                { label: "Limpar" },
              ],
              onClick: [
                (index: number, fp: any) => { if (index === 0) onClickHj(fp); },
                ...(onClickMes ? [(index: number, fp: any) => { if (index === 1) onClickMes(fp); }] : []),
                ...(onClickAno ? [(index: number, fp: any) => { if (index === (onClickMes ? 2 : 1)) onClickAno(fp); }] : []),
                (index: number, fp: any) => { if (index === (
                  1 + (onClickMes ? 1 : 0) + (onClickAno ? 1 : 0)
                )) onClickClean(fp); },
              ]
            })
          ] : []
        });

        if (flatpickr.l10ns?.default) {
          flatpickr.l10ns.default.firstDayOfWeek = 1;
        }

        return () => {
          if (!Array.isArray(flatPickr)) {
            flatPickr.destroy();
          }
        };
      } catch (error) {
        console.error('Erro ao carregar flatpickr:', error);
      }
    };

    initFlatpickr();
  }, [mounted, mode, onChange, id, defaultDate, dateFormat, onClickAno, onClickMes, onClickHj, onClickClean]);

  if (!mounted) {
    return (
      <div>
        {label && <Label htmlFor={id}>{label}</Label>}
        <div className="relative">
          <input
            id={id}
            placeholder={placeholder}
            className="h-11 w-full rounded-lg border appearance-none px-4 py-2.5 text-sm shadow-theme-xs placeholder:text-gray-400 focus:outline-hidden focus:ring-3 dark:bg-gray-900 dark:text-white/90 dark:placeholder:text-white/30 bg-transparent text-gray-800 border-gray-300 focus:border-brand-300 focus:ring-brand-500/20 dark:border-gray-700 dark:focus:border-brand-800"
            disabled
          />
          <span className="absolute text-gray-500 -translate-y-1/2 pointer-events-none right-3 top-1/2 dark:text-gray-400">
            <CalenderIcon className="size-6" />
          </span>
        </div>
      </div>
    );
  }

  return (
    <div>
      {label && <Label htmlFor={id}>{label}</Label>}

      <div className="relative">
        <input
          id={id}
          placeholder={placeholder}
          className="h-11 w-full rounded-lg border appearance-none px-4 py-2.5 text-sm shadow-theme-xs placeholder:text-gray-400 focus:outline-hidden focus:ring-3  dark:bg-gray-900 dark:text-white/90 dark:placeholder:text-white/30  bg-transparent text-gray-800 border-gray-300 focus:border-brand-300 focus:ring-brand-500/20 dark:border-gray-700  dark:focus:border-brand-800"
        />

        <span className="absolute text-gray-500 -translate-y-1/2 pointer-events-none right-3 top-1/2 dark:text-gray-400">
          <CalenderIcon className="size-6" />
        </span>
      </div>
    </div>
  );
}
